/***/ (function(module, exports) {

	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.papascott.de/archives/2001/09/12/the-day-the-earth-stood-still/\">After 8 years</a>, I think I've found peace. Inner peace, anyway. Outer peace is so hard to find these days. Just ask anyone in Afghanistan.</p>\n<p>But what has changed in that time? Not much. The <a href=\"http://en.wikipedia.org/wiki/Hamburg_cell#Background\">Marienstrasse</a> is still in Hamburg, the <a href=\"http://en.wikipedia.org/wiki/Al-Quds_Mosque\">Al-Quds mosque</a> too. We've sent soldiers abroad, more soldiers in Iraq have fallen than those who fell that day into thin air.</p>\n<p>This summer as we were in New York, we took the subway to Ground Zero, to catch a glimpse of hallowed ground. It was a tourist spot, with hawkers and food stands, but inside the fences, even on Saturday, the site was <em>alive</em> with construction work. That comforted me somehow, and we moved on.</p>\n<p>But what has changed? Nothing but the passage of time. Time closes all wounds, but are do the wounds really heal? All wounds leave a scar. I wish I could <a href=\"http://www.papascott.de/archives/2006/09/10/five-years/\">forget that day</a>, but time moves on and the scars remain.</p>\n<p>And so it goes.</p>","fields":{"title":"Inner Peace","date":"11 Sep 2009","url":"https://papascott.de/2009-09-11-inner-peace/"}}},"pathContext":{"slug":"/2009-09-11-inner-peace/","prev":{"fields":{"title":"I Gotta Feeling","date":"10 Sep 2009","slug":"/2009-09-10-i-gotta-feeling/"}},"next":{"fields":{"title":"Exit Now!","date":"12 Sep 2009","slug":"/2009-09-12-exit-now/"}}}}

/***/ })

